import React, { useEffect, useRef, useState } from "react";
import AiBot from "../AiBot";
import ppt from "../../images/summary-ppt.pdf";
import { useInView } from "react-intersection-observer";
import "./style.scss";

const Product: React.FC = () => {
  const [selected, setSelected] = useState(0);
  const [loaded, setLoaded] = useState(false);
  const timer = useRef();
  const { ref, inView } = useInView({
    triggerOnce: true,
    rootMargin: "-100px 0px",
  });
  const sliders = [
    {
      title: "AI + 知识库",
      detail: "结合人工智能和知识库，实现自动化的信息检索和知识发现",
      messages: [
        {
          content: "如何获取专利授权",
          type: "text",
          sender: "user",
        },
        {
          content:
            "获取专利授权的过程包括以下几个主要步骤：\n准备专利申请文件：专利说明书、权利要求书、摘要\n提交专利申请:根据发明类型（如发明专利、实用新型专利、外观设计专利），向国家知识产权局（中国为国家知识产权局，国外为各国的专利机构）提交专利申请,并缴纳缴纳申请费\n专利审查包括形式审查，检查申请文件是否符合规定的形式要求；实质审查（适用于发明专利），对发明的新颖性、创造性和实用性进行实质性审查。实用新型和外观设计专利通常不进行实质审查。\n公开与公示:发明专利申请自申请日起18个月内进行公开。\n授权与公告：通过审查后，专利局会发出授权通知书，申请人需要缴纳授权登记费和印刷费，缴费后，专利局颁发专利证书，并在专利公报上公告\n维持专利:专利授权后，需要按年缴纳年费，以维持专利权的有效性。",
          type: "text",
          sender: "bot",
        },
      ],
    },
    {
      title: "AI + 办公",
      detail:
        "根据用户输入的文本、数据和图片，自动生成专业、美观的PPT演示文稿，涵盖各类主题和风格",
      messages: [
        {
          content: "请帮我设计一份简约风格的工作汇报PPT模板",
          type: "text",
          sender: "user",
        },
        {
          content: ppt,
          type: "file",
          sender: "bot",
        },
      ],
    },
    {
      title: "AI + 生活",
      detail:
        "根据用户的个人喜好和需求，提供定制化的服务和建议，满足多样化的生活需求。",
      messages: [
        {
          content: "我想花一周的时间到湖北游玩，请帮我设计一份旅游攻略。",
          type: "text",
          sender: "user",
        },
        {
          content:
            "第一天：抵达武汉。乘飞机或高铁抵达武汉天河国际机场或武汉火车站。选择市中心的酒店，方便出行。尝试武汉特色美食，参观江汉路步行街，感受武汉夜生活。\n第二天：武汉市内游览。上午，登武汉市最著名的地标性建筑黄鹤楼，可俯瞰武汉全景；游户部巷，品尝地道的武汉小吃。下午，参观武汉大学，欣赏校园美景，散步和骑行，游览中国最大的城中湖-东湖风景区。晚上，乘坐长江夜游船，欣赏两岸夜景。\n第三天：武汉 -> 宜昌。乘坐高铁（约2小时）从武汉到宜昌。游览世界上最大的水电站三峡大坝，了解大坝的建设和运营。下午，逛三峡人家风景区，感受三峡地区的自然风光和人文风情。晚餐，尝试宜昌特色菜，如抬格子和蒸肉糕。\n第四天：宜昌 -> 神农架。乘坐巴士或包车前往神农架（约4小时）。赴神农架自然保护区，探索原始森林，寻找野生动物的踪迹，登顶神农架最高峰，享受壮丽的山景。住农架林区酒店或山间民宿，尝试当地山珍，如野菜和香菇。\n第五天：神农架 -> 恩施。上午，徒步游玩恩施大峡谷，欣赏壮丽的自然景观。下午，乘船游览清江，感受如画的江景。第六天：恩施 -> 武汉。到梭布垭石林，探索独特的喀斯特地貌，欣赏奇异的石林景观。乘坐高铁（约5小时）返回武汉。\n第七天：武汉市区游览和返程。上午，游湖北省博物馆，了解湖北的历史文化，参观编钟表演。楚河汉街步行，体验现代商业街区，购物和用餐。下午，散步汉口江滩，感受江边风光。根据航班或列车时间安排返程，结束愉快的湖北之旅。",
          type: "text",
          sender: "bot",
        },
      ],
    },
  ];

  useEffect(() => {
    if (!loaded) return;
    setLoaded(false);
    timer.current = setTimeout(() => {
      setSelected((current) => (current + 1) % 3);
    }, 8000);
  }, [loaded]);

  return (
    <section className="product" ref={ref} id="product">
      <h2 className="section-title">产品服务</h2>
      {inView && (
        <div className="carousel">
          <ul className="control">
            {sliders.map((value, index) => {
              return (
                <li
                  className={`control-item ${
                    selected === index ? "active" : ""
                  }`}
                  onClick={() => {
                    clearTimeout(timer.current);
                    setSelected(index);
                  }}
                >
                  <h1 className="control-title">{value.title}</h1>
                  <p className="control-detail">{value.detail}</p>
                </li>
              );
            })}
          </ul>
          <ul className="slider">
            {sliders.map((value, index) => {
              return (
                <li
                  className={`slider-item ${
                    selected === index ? "active" : ""
                  }`}
                >
                  <AiBot
                    loaded={selected === index}
                    demoInfo={value.messages}
                    onLoaded={() => {
                      selected === index && setLoaded(true);
                    }}
                  />
                </li>
              );
            })}
          </ul>
        </div>
      )}
    </section>
  );
};

export default Product;
