import React, { useEffect, useState } from "react";

type TypeWriterType = {
  delay?: string;
  message: string;
  className?: string;
  speed?: number;
  onFinished?: () => void;
};

const TypeWriter: React.FC<TypeWriterType> = ({
  message,
  className,
  speed = 100,
  onFinished,
}) => {
  const [current, setCurrent] = useState("");
  const [index, setIndex] = useState(0);
  const step = 3;
  useEffect(() => {
    const timer = setTimeout(() => {
      if (current === message) {
        clearTimeout(timer);
        onFinished && onFinished();
      } else {
        setCurrent(message.slice(0, step * index));
        setIndex((v) => v + 1);
      }
    }, speed);
  }, [current, index]);
  return (
    <div className={className}>
      {current.split("\n").map((line, i) => (
        <p key={i} className="answer-content">
          {line}
        </p>
      ))}
    </div>
  );
};

export default TypeWriter;
