import React, { ReactNode, useEffect, useState } from "react";
import "./style.scss";
import { Document, Page, pdfjs } from "react-pdf";
import "react-pdf/dist/Page/AnnotationLayer.css";
import "react-pdf/dist/Page/TextLayer.css";
import TypeWriter from "../Typewriter";

pdfjs.GlobalWorkerOptions.workerSrc = new URL(
  "pdfjs-dist/build/pdf.worker.min.mjs",
  import.meta.url
).toString();

const options = {
  cMapUrl: "/cmaps/",
};

type MessageType = {
  content: string;
  type: string;
  sender: string;
};

type AiBotType = {
  demoInfo: Array<MessageType>;
  loaded: boolean;
  onLoaded: () => void;
};

const AiBot: React.FC<AiBotType> = ({ demoInfo, loaded, onLoaded }) => {
  const [numPages, setNumPages] = useState<number>(0);
  const [finished, setFinished] = useState(false);
  const [messages, setMessages] = useState<
    Array<{ content: string; type: string; sender: string }>
  >([]);

  function onDocumentLoadSuccess({ numPages }: { numPages: number }): void {
    onLoaded();
    setNumPages(numPages);
  }

  useEffect(() => {
    if (!loaded) return;
    if (messages.length === 0) {
      setMessages(demoInfo.slice(0, 1));
    }
    if (messages.length === 1 && finished) {
      setMessages(demoInfo.slice(0, 2));
    }
  }, [messages, finished, loaded]);

  useEffect(() => {
    if (loaded) {
      setMessages([]);
      setFinished(false);
    }
  }, [loaded]);

  const messageRenders: Record<
    string,
    (msg: MessageType, index: number) => ReactNode
  > = {
    text: (msg: MessageType, index: number) => (
      <TypeWriter
        key={index}
        className={`chat-message ${msg.sender}`}
        message={msg.content}
        onFinished={() => {
          if (index === 0) return setFinished(true);
          return onLoaded();
        }}
      />
    ),
    file: (msg: MessageType, index: number) => (
      <div key={index} className={`chat-message ${msg.sender}`}>
        <Document
          options={options}
          file={msg.content}
          onLoadSuccess={onDocumentLoadSuccess}
          loading={<div className="loading"></div>}
        >
          {numPages &&
            Array.from({ length: numPages }).map((v, pageNumber) => (
              <Page pageNumber={pageNumber + 1} />
            ))}
        </Document>
      </div>
    ),
  };

  return (
    <div className="chat-container">
      {messages.map((msg, index) => messageRenders[msg.type](msg, index))}
    </div>
  );
};

export default AiBot;
